import React, { useState, useEffect } from "react";
import {
  Button,
  Card,
  CircularProgress,
  Typography,
  TextField,
  Box,
} from "@mui/material";
import { toast } from "react-toastify";
import { aftBankTransfer } from "../../api/login/Login";
import AftPageLayout from "./AftPageLayout";
import { motion } from "framer-motion";

const AftBankTransfer = () => {
  const [mobileNumber, setMobile] = useState("");
  const [loading, setLoading] = useState(false);

  useEffect(() => {
    const savedMobile = localStorage.getItem("mobileNumber")?.slice(-10);
    if (savedMobile) setMobile(savedMobile);
  }, []);

  const handleSubmit = async () => {
    if (!/^[6-9]\d{9}$/.test(mobileNumber)) {
      toast.error("Enter valid 10 digit mobile number");
      return;
    }

    setLoading(true);
    localStorage.setItem("mobileNumber", mobileNumber);

    try {
      const res = await aftBankTransfer({ mobileNumber });
      const response = res?.data;

      if (response?.statuscode === "GRU") {
        window.location.href = response?.data?.abRedirectUrl;
        return;
      }

      if (response?.error === false) {
        toast.success(response?.status || "Success");
      } else {
        toast.error(response?.status || "AFT Failed");
      }
    } catch (error) {
      toast.error(error?.response?.data?.message || "AFT request failed");
    } finally {
      setLoading(false);
    }
  };

  return (
    <AftPageLayout title="AFT Bank Transfer">
      <Box
        sx={{
          mt: 4,
          display: "flex",
          justifyContent: "center",
          px: 2,
        }}
      >
        <motion.div
          initial={{ opacity: 0, y: 30 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.5 }}
          style={{ width: "100%", maxWidth: 420 }}
        >
          {/* Top Gradient Card */}
          <Card
            sx={{
              p: 4,
              borderRadius: "24px",
              background: "linear-gradient(135deg, #ffffff, #f8f9fb)",
              boxShadow: "0 6px 20px rgba(0,0,0,0.08)",
              backdropFilter: "blur(6px)",
            }}
          >
            {/* Title */}
            <Typography
              fontSize="22px"
              fontWeight="700"
              sx={{
                color: "#193968",
                mb: 1,
              }}
            >
              Bank Transfer (AFT)
            </Typography>

            <Typography
              fontSize="14px"
              sx={{ color: "#5a7191", mb: 3 }}
            >
              Enter your Airtel-registered mobile number to continue.
            </Typography>

            {/* Input */}
            <TextField
              fullWidth
              className="mui-autofill-fix"
              label="Mobile Number"
              value={mobileNumber}
              onChange={(e) => setMobile(e.target.value.replace(/\D/g, ""))}
              inputProps={{ maxLength: 10 }}
              sx={{
                mb: 3,
                "& .MuiOutlinedInput-root": {
                  borderRadius: "14px",
                  background: "#fff",
                },
              }}
            />

            {/* Info Box */}
            <Box
              sx={{
                background: "#aeb9c715",
                borderLeft: "4px solid #cc9840",
                p: 2,
                borderRadius: "12px",
                mb: 3,
              }}
            >
              <Typography sx={{ fontSize: "13px", color: "#193968" }}>
                ⚠️ <b>Note:</b> Enter only the Airtel mobile number registered with
                Airtel Payments Bank.
              </Typography>

              <Typography sx={{ fontSize: "12px", mt: 0.5, color: "#5a7191" }}>
                नोट: कृपया वही एयरटेल मोबाइल नंबर दर्ज करें जो एयरटेल पेमेंट्स बैंक
                से पंजीकृत हो।
              </Typography>
            </Box>

            {/* Submit Button */}
            <Button
              fullWidth
              variant="contained"
              disabled={loading}
              onClick={handleSubmit}
              sx={{
                py: 1.5,
                borderRadius: "50px",
                fontWeight: 700,
                background: "#193968",
                color: "#fff",
                "&:hover": {
                  background: "#163258",
                },
              }}
            >
              {loading ? <CircularProgress size={22} sx={{ color: "#fff" }} /> : "Submit AFT"}
            </Button>
          </Card>
        </motion.div>
      </Box>
    </AftPageLayout>
  );
};

export default AftBankTransfer;
